@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
.App {
  width: 400px;
  margin: auto;
  font-family: 'Lato', sans-serif;
}

.buttonNumber {
  height: 30px;
  width: 30px;
  background-color: rgb(255, 255, 204);
  border: 1px solid rgb(255, 236, 128);
  margin-right: 3px;
}

.buttonNumber--clicked {
  background-color: rgb(255, 224, 87);
}

li {
  text-decoration: none;
  list-style-type: none;
}

.numbersContainer {
  display: flex;
  flex-direction: row;
  width: 300px;  
  margin-top: 20px;
  margin-bottom: 20px;
}

.numberBall {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: cornflowerblue;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
  color: white;
}

.numberBall--match {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(237, 100, 100);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
  color: white;
}

